import axios from 'axios'


export const axiosApiInstance = axios.create();

// Request interceptor for API calls
axiosApiInstance.interceptors.request.use(
    async config => {
        config.headers = {
            'Authorization': `Bearer ${sessionStorage.getItem('TokenInfo') === null ? "demoooooo" : (JSON.parse(sessionStorage.getItem('TokenInfo')))['token']}`,
            'Accept': 'application/json',
            'Content-Type': 'application/x-www-form-urlencoded'
        }
        return config;
    },
    error => {
        Promise.reject(error)
    });

// // Response interceptor for API calls
axiosApiInstance.interceptors.response.use(response => {
    return response;
}, err => {
    return new Promise((resolve, reject) => {
        const originalReq = err.config;
        if (err.response.status === 401 && err.config && !err.config.__isRetryRequest) {
            originalReq.__isRetryRequest = true;
            var myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
            myHeaders.append("Cookie", "__Host-device_id=AQDC0tJ-A0RMFGOtJ02giNDMfioSXzOLL3JKgbgI74Z9NBpVRlqf0WDC0NoRbmkYiaMjkQK1QQf1D1xXPcvwEiMJStkGkNQiGYc; sp_t=b8d3612b89cb15ca3696bccdf32b651b");

            var urlencoded = new URLSearchParams();
            urlencoded.append("grant_type", "refresh_token");
            urlencoded.append("refresh_token", "AQDHIGsyxv5Wp8uKj7rtuBry-wWjXB_a9CnJxG-oolYUm9BSryUw5dUJe4kSzANBVn6aU4yFp3DLF5IFL58p0xg3tZewWxdVttVHBhhtbzW7MkC55s2SIWhNgLSOv4JfX2s");
            urlencoded.append("client_id", "654c7484fce14022bd0ec15f605a0bd3");
            urlencoded.append("client_secret", "80455513ed344fd5b8c96667ae6a6e8c");

            let res = fetch('https://accounts.spotify.com/api/token', {
                method: 'POST',
                mode: 'cors',
                cache: 'no-cache',
                credentials: 'same-origin',
                headers: myHeaders,
                redirect: 'follow',
                referrer: 'no-referrer',
                body: urlencoded,
            }).then(res => res.json()).then(res => {
                console.log(res);
                sessionStorage.setItem('TokenInfo', JSON.stringify({ token: res.access_token, ref_token_exp_time: new Date(Date.now()).setTime(new Date(Date.now()).getTime() + 3600000) }));
                originalReq.headers['Token'] = res.access_token;
                originalReq.headers['Device'] = "device";
                return axiosApiInstance(originalReq);
            });
            resolve(res);
        }
        return Promise.reject(err);
    });
});

Date.prototype.addHours = function (h) {
    this.setTime(this.getTime() + (h * 60 * 60 * 1000));
    return this;
}
