    const handleCategoryChange = (cattegoryId, categoryName) => {
        setSelectedCat(cattegoryId);
        axiosApiInstance.get(`${config.PlayList.replace('cattegoryId', cattegoryId)}`, { headers: { Authorization: `Bearer ${config.Token}` } })
            .then(response => {
                setSelectedCatItems({ items: response.data.playlists.items, cateName: categoryName });
            })
            .catch((error) => {
                console.log('error ' + error);
            });
    }
    const handlePlayListChange = (playlist) => {
        // setSelectedCat(cattegoryId);
        if (playlist.type === 'track') {
            onTrackSelect(playlist);
            return;
        }
        let url = `https://api.spotify.com/v1/${playlist.type}s/${playlist.id}`;
        url = playlist.type == 'artist' ? `${url}/top-tracks?country=IN` : `${url}/tracks`
        // url = playlist.type === "playlist" ? playlist.tracks.href : playlist.href;
        axiosApiInstance.get(`${url}`, { headers: { Authorization: `Bearer ${config.Token}` } })
            .then(response => {
                if (playlist.type == 'artist') {
                    setTracks({ playlistData: playlist, tracksData: { items: response.data.tracks } });
                } else {
                    setTracks({ playlistData: playlist, tracksData: response.data });
                }
            })
            .catch((error) => {
                console.log('error ' + error);
            });
    }
